import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faMobileAlt } from '@fortawesome/free-solid-svg-icons'

export default class HomeSectionContact extends Component {
    constructor(props) {
        super(props);

        this.state = {
            userEmail: '',
            userPhone: '',
            message: ''
        }
    }

    handleUserEmail (e) {
        let userEmail = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            userEmail
        }));
    }

    handleUserPhone (e) {
        let userPhone = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            userPhone
        }));
    }

    handleMessage (e) {
        let message = e.target.value;
        this.setState(prevState => ({
            ...prevState,
            message
        }));
    }

    onContactFormSubmit (e) {
        e.preventDefault();
    }
    render () {
        return (
            <div className="section">
                <h2 className="title text-primary--dark text-center">Contacto</h2>

                <div className="container">
                    <form onSubmit={this.onContactFormSubmit}>

                        <div className="form-group">
                            <label htmlFor="userEmail">e-mail:</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <FontAwesomeIcon icon={faEnvelope} />
                                    </div>
                                </div>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="userEmail"
                                    aria-describedby="emailHelp"
                                    onChange={this.handleUserEmail.bind(this)} />
                            </div>
                        </div>

                        <div className="form-group">
                            <label htmlFor="userPhone">Teléfono:</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <FontAwesomeIcon icon={faMobileAlt} />
                                    </div>
                                </div>
                                <input
                                    type="tel"
                                    className="form-control"
                                    id="userPhone"
                                    onChange={this.handleUserPhone.bind(this)} />
                            </div>
                        </div>
                        <div className="form-group">
                            <label htmlFor="message">Mensaje: </label>
                            <textarea
                                className="form-control"
                                placeholder="Ingresá tu mensaje"
                                id="message"
                                onChange={this.handleMessage.bind(this)} />
                        </div>
                        <button type="submit" className="btn btn-primary">Enviar</button>
                    </form>

                </div>
            </div>
        );
    }
}
